.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.ButtonStyle {
  display: flex;
  color: white;
  font-weight: bold;
  outline: none;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  text-align : center;
  justify-content : center;
  align-items: center;

  height: 2.25rem;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1.2rem;
  background: #069612;
}

.ButtonAlign{
  display: flex;
  justify-content : center;
  align-items: center;
  flex-direction: row;
}